function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
  }


function formatTime(timestamp){
    let date = new Date(timestamp * 1000);
    let formatedDate =
        date.getDate() + "." +
        (date.getMonth()+1) + "." +
        date.getFullYear() + " - " +
        date.getHours() + ":" +
        date.getMinutes() + ":" +
        date.getSeconds()
        ;
    return formatedDate;

}


